import React from 'react'
import {NavLink} from 'react-router-dom'
import './../Dialogs.scss'

type PropsType = {
    id: number,
    name: string
}

const DialogItem: React.FC<PropsType> = ({id, name}) => {
    return (
        <div key={id}><NavLink to={'/dialogs/'+ id} className={'dialog'}>{name}</NavLink></div>
    )
}

export default DialogItem